.atr_picture-bg {
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  z-index: -1;
}
.atr_picture-bg img {
  height: auto;
  width: 100%;
  object-fit: cover;
}

.atr_button {
  background-color: #d3e3f3;
  border: none;
  border-radius: 2px;
  color: #255891;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 2px;
  margin-top: 40px;
  padding: 6px 40px;
  text-align: center;
  transition: background-color 0.2s ease;
}
.atr_button:hover {
  background-color: #f5f5f5;
}

.atr_h2 {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 80px;
  line-height: 1.1;
}
.atr_h2 span {
  display: block;
}
.atr_h2 .atr_h-small {
  color: #f5f5f5;
  font-weight: 300;
  letter-spacing: 1px;
  margin: 0;
  text-align: center;
  font-size: 48px;
  line-height: 1.5;
}

.atr_offleft {
  position: relative;
  right: 120px;
}

@media only screen and (max-width: 992px) {
  .atr_h2 {
    font-size: 70px;
  }
  .atr_h2 .atr_h-small {
    font-size: 42px;
  }

  .atr_offleft {
    right: 100px;
  }

  .atr_button {
    font-size: 12px;
    padding: 6px 30px;
  }
}
@media only screen and (max-width: 768px) {
  .atr_h2 {
    font-size: 60px;
  }
  .atr_h2 .atr_h-small {
    font-size: 36px;
  }

  .atr_offleft {
    right: 80px;
  }
}
@media only screen and (max-width: 576px) {
  .atr_h2 {
    font-size: 42px;
  }
  .atr_h2 .atr_h-small {
    font-size: 24px;
  }

  .atr_offleft {
    right: 60px;
  }
}
